> div.avatar-container {
    @include upload-box;

    width: 8rem;
    height: 8rem;
    border-radius: 50%;

    @media screen and (max-width: 40rem) {
        margin: 0 auto 1rem; }

    > img.avatar {
        position: absolute;
        display: block;
        max-width: 8rem;
        max-height: 8rem;
        user-select: none; }

    > svg.upload {
        visibility: hidden;

        width: 6rem;
        height: 6rem; }

    > input.file-selector {
        width: 8rem;
        height: 8rem; }

    &:not(.uploading) {
        &:hover, &.drag {
            > img.avatar {
                opacity: .25; }

            > svg.upload {
                visibility: visible; } }

        &.no-avatar {
            > img.avatar {
                visibility: hidden; }

            > svg.upload {
                visibility: visible;
                opacity: .5; } } }

    &.uploading {
        > img.avatar {
            opacity: .25; } } }
