> nav.sidebar {
    grid-area: sidebar;
    background-color: white;

    padding: .5rem;

    overflow-y: auto;

    div.buttons {
        margin-bottom: 1.5rem;

        button {
            @include button;
            background-color: white;
            width: 100%; } }

    div.list {
        &:not(:last-of-type) {
            margin-bottom: 1.5rem; }

        div.title {
            h2 {
                display: inline-block;
                margin: 0 0 .25rem 0;
                font-size: 1.25rem; }

            a {
                display: inline-block;
                float: right; } }

        a.entry {
            display: block;
            color: $text-color;
            text-decoration: none;
            padding: .25rem;
            border-radius: .25rem;
            height: 2rem;
            box-sizing: border-box;

            &:not(:hover) > svg {
                display: none; }

            > svg {
                float: right; }

            &:hover {
                background-color: $primary-light; }

            &.active {
                background-color: $primary;
                color: white; }

            &.client {
                img.avatar {
                    max-height: 1.5rem;
                    border-radius: 100%;
                    vertical-align: middle; } }

            span.displayname, span.id {
                margin-left: .25rem;
                vertical-align: middle; } } } }
