> div.client {
    display: flex;

    > div.sidebar {
        vertical-align: top;
        text-align: center;
        width: 8rem;
        margin-right: 1rem;

        > div {
            margin-bottom: 1rem; }

        @import "avatar";
        @import "started"; }

    > div.info {
        vertical-align: top;
        flex: 1;

        > div.instances {
            @include instancelist; } }

    @media screen and (max-width: 40rem) {
        flex-wrap: wrap;

        > div.sidebar, > div.info {
            width: 100%;
            margin-right: 0; } } }
