@import "dashboard-grid";

.dashboard {
    display: grid;
    height: 100%;
    max-width: 60rem;
    margin: auto;
    box-shadow: 0 .5rem .5rem rgba(0, 0, 0, 0.5);
    background-color: $background;

    > a.title {
        grid-area: title;
        background-color: white;
        display: flex;
        align-items: center;
        justify-content: center;

        font-size: 1.35rem;
        font-weight: bold;

        color: $text-color;
        text-decoration: none;

        > img {
            max-width: 2rem;
            margin-right: .5rem; } }

    > div.user {
        grid-area: user;
        background-color: white;
        border-bottom: 1px solid $border-color;
        display: flex;
        align-items: center;
        justify-content: center;
        span {
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: $primary;
            color: $inverted-text-color;
            margin: .375rem .5rem;
            width: 100%;
            height: calc(100% - .375rem);
            box-sizing: border-box;
            border-radius: .25rem; } }

    @import "sidebar";
    @import "topbar";

    @media screen and (max-width: 35rem) {
        &:not(.sidebar-open) > * {
            transform: translateX(-15rem); }
        > * {
            transition: transform 0.4s; } }

    > main.view {
        grid-area: main;
        border-left: 1px solid $border-color;

        overflow-y: auto;

        @import "client/index";
        @import "instance";
        @import "plugin";

        > div {
            margin: 2rem 4rem;

            @media screen and (max-width: 50rem) {
                margin: 2rem 1rem; } }

        > div.not-found, > div.home {
            text-align: center;
            margin-top: 5rem;
            font-size: 1.5rem; }

        div.buttons {
            @include button-group;
            display: flex;
            margin: 1rem .5rem;
            width: calc(100% - 1rem);

            button.open-log {
                @include button;
                @include main-color-button; } }

        div.error {
            @include notification($error);
            margin: 1rem .5rem;

            &:empty {
                display: none; } }

        button.delete {
            background-color: $error-light !important;

            &:hover {
                background-color: $error !important; } }

        button.save, button.delete {
            @include button;
            @include main-color-button;
            width: 100%;
            height: 2.5rem;
            padding: 0;

            > .spinner {
                @include thick-spinner;
                @include white-spinner;
                width: 2rem; } } } }
