.maubot-wrapper:not(.authenticated) {
    background-color: $primary;

    text-align: center; }

.login {
    width: 25rem;
    height: 23rem;
    display: inline-block;
    box-sizing: border-box;
    background-color: white;
    border-radius: .25rem;
    margin-top: 3rem;

    @media screen and (max-width: 27rem) {
        margin: 3rem 1rem 0;
        width: calc(100% - 2rem); }

    h1 {
        color: $primary;
        margin: 3rem 0; }

    input, button {
        margin: .5rem 2.5rem;
        height: 3rem;
        width: calc(100% - 5rem);
        box-sizing: border-box; }

    input {
        @include input; }

    button {
        @include button($width: calc(100% - 5rem), $height: 3rem, $padding: 0);
        @include main-color-button;

        .spinner {
            @include white-spinner;
            @include thick-spinner;
            width: 2rem; } }

    &.errored {
        height: 26.5rem;

        .error {
            @include notification($error);
            margin: .5rem 2.5rem; } } }
