body {
    font-family: $font-stack;
    margin: 0;
    padding: 0;
    font-size: 16px; }

#root {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0; }

.maubot-wrapper {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: $background-dark; }

.maubot-loading {
    margin-top: 10rem;
    width: 10rem; }
