> div.instance {
    > div.preference-table {
        .select-client {
            display: flex;
            align-items: center;

            img.avatar {
                max-height: 1.375rem;
                border-radius: 50%;
                margin-right: .5rem; } } }

    > div.ace_editor {
        z-index: 0;
        height: 15rem !important;
        width: calc(100% - 1rem) !important;
        font-size: 12px;
        font-family: "Fira Code", monospace;

        margin: .75rem .5rem 1.5rem; } }
