div.log {
    height: 100%;
    width: 100%;
    overflow: auto;

    > div.lines {
        text-align: left;
        font-size: 12px;
        max-height: 100%;
        min-width: 100%;
        font-family: "Fira Code", monospace;
        display: table;

        > div.row {
            display: table-row;
            white-space: pre;

            &.debug {
                background-color: $background;

                &:nth-child(odd) {
                    background-color: $background-dark; } }

            &.info {
                background-color: #AAFAFA;

                &:nth-child(odd) {
                    background-color: #66FAFA; } }

            &.warning, &.warn {
                background-color: #FABB77;

                &:nth-child(odd) {
                    background-color: #FAAA55; } }

            &.error {
                background-color: #FAAAAA;

                &:nth-child(odd) {
                    background-color: #FA9999; } }

            &.fatal {
                background-color: #CC44CC;

                &:nth-child(odd) {
                    background-color: #AA44AA; } }

            &.unfocused {
                opacity: .25; }

            > span {
                padding: .125rem .25rem;
                display: table-cell;

                &:first-child {
                    padding-left: 0; }

                &:last-child {
                    padding-right: 0; } }

            a {
                color: inherit;
                text-decoration: none;

                &:hover {
                    text-decoration: underline; } }

            > span.text {
                > div.content > * {
                    background-color: inherit !important;
                    margin: 0 !important; } } } } }
