@mixin button($width: null, $height: null, $padding: .375rem 1rem) {
    font-family: $font-stack;
    padding: $padding;
    width: $width;
    height: $height;
    background-color: $background;
    border: none;
    border-radius: .25rem;
    color: $text-color;
    box-sizing: border-box;
    font-size: 1rem;

    &.disabled-bg {
        background-color: $background-dark; }

    &:not(:disabled) {
        cursor: pointer;

        &:hover {
            background-color: darken($background, 10%); } } }

@mixin link-button() {
    display: inline-block;
    text-align: center;
    text-decoration: none; }

@mixin main-color-button() {
    background-color: $primary;
    color: $inverted-text-color;
    &:hover:not(:disabled) {
        background-color: $primary-dark; }

    &:disabled.disabled-bg {
        background-color: $background-dark !important;
        color: $text-color; } }

.button {
    @include button;

    &.main-color {
        @include main-color-button; } }

@mixin button-group() {
    width: 100%;
    display: flex;
    > button, > .button {
        flex: 1;

        &:first-of-type {
            margin-right: .5rem; }

        &:last-of-type {
            margin-left: .5rem; }

        &:first-of-type:last-of-type {
            margin: 0; } } }

@mixin vertical-button-group() {
    display: flex;
    flex-direction: column;
    > button, > .button {
        flex: 1;
        border-radius: 0;

        &:first-of-type {
            border-radius: .25rem .25rem 0 0; }

        &:last-of-type {
            border-radius: 0 0 .25rem .25rem; }

        &:first-of-type:last-of-type {
            border-radius: .25rem; } } }

@mixin input($width: null, $height: null, $vertical-padding: .375rem, $horizontal-padding: 1rem, $font-size: 1rem) {
    font-family: $font-stack;
    border: 1px solid $border-color;
    background-color: $background;
    color: $text-color;
    width: $width;
    height: $height;
    box-sizing: border-box;
    border-radius: .25rem;
    padding: $vertical-padding $horizontal-padding;
    font-size: $font-size;
    resize: vertical;

    &:hover, &:focus {
        border-color: $primary; }

    &:focus {
        border-width: 2px;
        padding: calc(#{$vertical-padding} - 1px) calc(#{$horizontal-padding} - 1px); } }

.input, .textarea {
    @include input; }

input {
    font-family: $font-stack; }

@mixin notification($border: $error-dark, $background: transparentize($error-light, 0.5)) {
    padding: 1rem;
    border-radius: .25rem;
    border: 2px solid $border;
    background-color: $background; }
