@mixin upload-box() {
    position: relative;
    overflow: hidden;

    display: flex;
    align-items: center;
    justify-content: center;

    > svg.upload {
        position: absolute;
        display: block;

        padding: 1rem;
        user-select: none; }


    > input.file-selector {
        position: absolute;
        user-select: none;
        opacity: 0; }

    > div.spinner {
        @include thick-spinner; }

    &:not(.uploading) {
        > input.file-selector {
            cursor: pointer; } } }
