> .plugin {
    > .upload-box {
        @include upload-box;

        width: calc(100% - 1rem);
        height: 10rem;
        margin: .5rem;
        border-radius: .5rem;
        box-sizing: border-box;

        border: .25rem dotted $primary;

        > svg.upload {
            width: 8rem;
            height: 8rem;

            opacity: .5; }

        > input.file-selector {
            width: 100%;
            height: 100%; }

        &:not(.uploading):hover, &:not(.uploading).drag {
            border: .25rem solid $primary;
            background-color: $primary-light;

            > svg.upload {
                opacity: 1; } }

        &.uploading {
            > svg.upload {
                visibility: hidden; }
            > input.file-selector {
                cursor: default; } } }

    > div.instances {
        @include instancelist; } }
